import React, { useEffect, useState } from 'react'
import Cookies from 'js-cookie'

import { docs } from 'constant'
import { Button } from 'components/ui'
import { Wrap, Emoji, Container, Text, WrapButton, PrivacyLink } from './Style'

const CookieConsent = () => {
  const [isConsentHidden, setConsentHidden] = useState<boolean>(true)

  useEffect(() => {
    const enabledCookies = Cookies.get('gdpr-analytics-enabled')

    setConsentHidden(Boolean(enabledCookies))
  }, [])

  const enableTracking = () => {
    Cookies.set('gdpr-analytics-enabled', 'true', {
      expires: 365,
    })

    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-ignore
    window.trackGoogleAnalytics()
    setConsentHidden(true)
  }

  const closeCookieConsent = () => {
    Cookies.set('gdpr-analytics-enabled', 'false')
    setConsentHidden(true)
  }

  if (isConsentHidden) {
    return null
  }

  return (
    <Wrap>
      <Emoji>🍪</Emoji>
      <Container>
        <Text>We&nbsp;use cookies to&nbsp;ensure you get the best personalized experience on&nbsp;our website</Text>
        <Text>
          For more info read <PrivacyLink href={docs.PRIVACY}>Privacy policy</PrivacyLink>
        </Text>
      </Container>
      <WrapButton>
        <Button type='default' size='medium' onClick={enableTracking}>
          I agree
        </Button>

        <Button type='outline' size='medium' onClick={closeCookieConsent}>
          Decline
        </Button>
      </WrapButton>
    </Wrap>
  )
}

export { CookieConsent }
