const ItemButton = css`
  position: relative;

  margin: 0;
  padding: 1.2rem 2.4rem;

  width: auto;
  height: fit-content;

  display: inline-flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  flex: 0 0 auto;
  gap: 0.8rem;

  font-weight: ${(props) => props.theme.font.weight.bold};

  font-size: 1.8rem;
  line-height: 1.5;

  text-decoration: none;

  color: ${(props) => props.theme.color.textTertiary};
  background-color: ${(props) => props.theme.color.default};
  border: none;
  border-radius: 6em;

  transition: all 0.26s ease-in;

  overflow: hidden;
  outline: none;
  z-index: ${(props) => props.theme.zIndex.minimal};
`

export const ItemLink = styled(Link)`
  ${ItemButton};
  width: 100%;
  font-size: 1.6rem;
  padding: 2rem 3rem;
  color: ${(props) => props.theme.color.textTertiary};
  background-color: ${(props) => props.theme.color.default};

  svg {
    transition: all 0.26s ease;
  }

  &:hover:not([disabled]),
  &:focus:not([disabled]),
  &:active:not([disabled]) {
    box-shadow: 0 1rem 1.8rem -0.9rem ${(props) => transparentize(0.63, props.theme.color.black)};
    svg {
      transform: rotate(-45deg);
    }
  }

  ${(props) => props.theme.breakpoints.greaterThan('sm')`
    width: 32rem;
    font-size: 1.8rem;
  `};
`

