import { SwitchButtonSize } from '../@types'

export const getSize = (size: SwitchButtonSize) =>
  ({
    default: {
      maxWidth: '36rem',
      fontSize: '1.4rem',
      lineHeight: 1.43,
      letterSpacing: '-0.015rem',
    },
    medium: {
      maxWidth: '100%',
      fontSize: '1.4rem',
      lineHeight: 1.43,
      letterSpacing: '-0.015rem',
    },
    small: {
      maxWidth: '26.2rem',
      fontSize: '1.2rem',
      lineHeight: 1.33,
      letterSpacing: '0.006rem',
    },
  }[size])
