import React, { useEffect, useMemo, useRef, useState } from 'react'

import axios from 'axios'

import { formatPrice } from 'utils'
import { BALANCES_API, menu, numbers } from 'constant'

import { Icon, Loader } from 'components/ui'
import {
  Item,
  Days,
  Title,
  Section,
  SubTitle,
  ItemLink,
  Container,
  TitleWrap,
  ItemsWrap,
  ItemTitle,
  TimerWrap,
  Hourglass,
  ProgressBar,
  ProgressText,
  Parallelogram,
  FundraiseInfo,
  FundraiseNeed,
  DaysDescription,
  FundraiseBalance,
  ProgressContainer,
  FundraiseDescription,
} from './Style'

const TRIAL_YEAR = 2025
const TRIAL_MONTH = 8
const TRIAL_DAY = 11

const MINUTE = 1000 * 60
const DAY = 1000 * 60 * 60 * 24

function Trial() {
  const fundsFetcherTimer = useRef<NodeJS.Timeout | null>(null)
  const daysCalculatorTimer = useRef<NodeJS.Timeout | null>(null)

  const [progress, setProgress] = useState(0)
  const [daysUntil, setDaysUntil] = useState(0)
  const [currentFunded, setFunded] = useState(0)

  const formattedFundsRequired = useMemo(() => {
    return formatPrice(numbers.FOR_TRIAL_FUNDS_REQUIRED)
  }, [])

  const formattedCurrentFunded = useMemo(() => {
    if (!currentFunded) {
      return null
    }
    return formatPrice(currentFunded)
  }, [currentFunded])

  const calculateDaysUntil = () => {
    const now = new Date()
    const targetDate = new Date(Date.UTC(TRIAL_YEAR, TRIAL_MONTH - 1, TRIAL_DAY))

    const timeDifference = targetDate.getTime() - now.getTime()

    return timeDifference > 0 ? Math.ceil(timeDifference / DAY) : 0
  }

  useEffect(() => {
    daysUntilCalculator()
    fundsFetcher()

    return () => {
      if (fundsFetcherTimer.current) {
        clearTimeout(fundsFetcherTimer.current)
      }

      if (daysCalculatorTimer.current) {
        clearTimeout(daysCalculatorTimer.current)
      }
    }
  }, [])

  const daysUntilCalculator = () => {
    setDaysUntil(calculateDaysUntil())
    fundsFetcherTimer.current = setTimeout(daysUntilCalculator, MINUTE)
  }

  const getProgress = (funded: number) => {
    setProgress(Math.ceil(funded / (numbers.FOR_TRIAL_FUNDS_REQUIRED / 100)))
  }

  const fundsFetcher = async (retry = 0) => {
    try {
      const controller = new AbortController()

      const timeout = setTimeout(() => {
        controller.abort('Time out')
      }, 5_000)

      const { data: balance } = await axios.get<{
        totalFundraise: string
        communityFundraise: string
      }>(BALANCES_API, { signal: controller.signal })

      clearTimeout(timeout)

      setFunded(+balance.totalFundraise)
      getProgress(+balance.totalFundraise)
    } catch {
      if (retry < 5) {
        setTimeout(() => {
          fundsFetcher(retry + 1)
        }, 1000)
      }
    }
  }

  return (
    <Section id='trial'>
      <Container>
        <TitleWrap>
          <Title>Estimated time until trial end</Title>
          <TimerWrap>
            <Parallelogram>
              <Hourglass src='/images/trial/hourglass.webp' alt='Hourglass' />
            </Parallelogram>
            <Parallelogram>
              <Days>{daysUntil}</Days>
              <DaysDescription>days</DaysDescription>
            </Parallelogram>
          </TimerWrap>
        </TitleWrap>

        <ItemsWrap>
          <Item>
            <ItemTitle>Legal defense fund support</ItemTitle>
            <FundraiseInfo>
              <ProgressContainer>
                <ProgressBar percentage={progress}>
                  <ProgressText>{progress}%</ProgressText>
                </ProgressBar>
              </ProgressContainer>
              <FundraiseDescription>
                <FundraiseBalance>
                  {formattedCurrentFunded || <Loader />} <SubTitle>balance</SubTitle>
                </FundraiseBalance>
                <span>/</span>
                <FundraiseNeed>
                  {formattedFundsRequired} <SubTitle>need</SubTitle>
                </FundraiseNeed>
              </FundraiseDescription>
            </FundraiseInfo>
            <ItemLink to={menu.donateRoman}>
              Donate to&nbsp;Roman <Icon name='arrow' rotate='-90deg' />
            </ItemLink>
          </Item>
        </ItemsWrap>
      </Container>
    </Section>
  )
}

export { Trial }
