import React from 'react'
import { useStaticQuery, graphql } from 'gatsby'

import { menu } from 'constant'
import { Icon } from 'components/ui'

import {
  Section,
  Container,
  WrapImage,
  WrapperImage,
  StyledImg,
  Content,
  Title,
  FirstWord,
  SecondWord,
  DonateLink,
} from './Style'

function Banner() {
  const data = useStaticQuery(graphql`
    query {
      first: file(relativePath: { eq: "images/banner/main-roman.webp" }) {
        childImageSharp {
          fluid(maxWidth: 374, quality: 100) {
            ...GatsbyImageSharpFluid_withWebp
          }
        }
      }
      second: file(relativePath: { eq: "images/banner/main-alex.webp" }) {
        childImageSharp {
          fluid(maxWidth: 374, quality: 100) {
            ...GatsbyImageSharpFluid_withWebp
          }
        }
      }
    }
  `)

  return (
    <Section>
      <Container>
        <Content>
          <Title>
            <FirstWord>Defend the right</FirstWord>
            <SecondWord>to privacy in Web3</SecondWord>
          </Title>

          <DonateLink to={menu.donate}>
            Donate
            <Icon name='arrow' rotate='-90deg' />
          </DonateLink>
        </Content>

        <WrapImage>
          <WrapperImage type='first'>
            <StyledImg fluid={data.first.childImageSharp.fluid} alt='Banner Roman Storm image' />
          </WrapperImage>
          <WrapperImage type='second'>
            <StyledImg fluid={data.second.childImageSharp.fluid} alt='Banner Alex Pertsev image' />
          </WrapperImage>
        </WrapImage>
      </Container>
    </Section>
  )
}

export { Banner }
