import React, { useState, useRef } from 'react'
import { useStaticQuery, graphql } from 'gatsby'

import { menu, numbers } from 'constant'

import { Icon } from 'components/ui'
import {
  Section,
  Wrap,
  DonateLink,
  Container,
  Info,
  Title,
  InfoWrap,
  InfoText,
  Composition,
  ImgWrap,
  StyledImage,
} from './Style'

const Whois = () => {
  const [show, setShow] = useState(false)
  const timeoutRef = useRef(null)

  function resetTimeout() {
    if (timeoutRef.current) {
      clearTimeout(timeoutRef.current)
    }
  }

  React.useEffect(() => {
    resetTimeout()
    // @ts-ignore
    timeoutRef.current = setTimeout(() => setShow(!show), numbers.CHANGE_IMAGE_DELAY)

    return () => {
      resetTimeout()
    }
  }, [show])

  const data = useStaticQuery(graphql`
    query {
      alex: file(relativePath: { eq: "images/whois/whois-alex.webp" }) {
        childImageSharp {
          fluid(maxWidth: 560, quality: 100) {
            ...GatsbyImageSharpFluid_withWebp
          }
        }
      }
      roman: file(relativePath: { eq: "images/whois/whois-roman.webp" }) {
        childImageSharp {
          fluid(maxWidth: 560, quality: 100) {
            ...GatsbyImageSharpFluid_withWebp
          }
        }
      }
    }
  `)

  return (
    <Section id='whoIs'>
      <Wrap>
        <Container>
          <Title>Who we are</Title>
          <Info>
            <InfoWrap>
              <InfoText>
                We&nbsp;at&nbsp;JusticeDAO, Inc. (&laquo;JusticeDAO&raquo;) are on&nbsp;the frontlines of&nbsp;the
                defense effort in&nbsp;legal matters related to&nbsp;Tornado Cash. We&nbsp;have engaged
                an&nbsp;international team of&nbsp;legal counsel to&nbsp;represent the original&nbsp;TC protocol
                developers, specifically&nbsp;&mdash; Alexey Pertsev and Roman Storm.
              </InfoText>
              <InfoText>
                In&nbsp;order to&nbsp;ensure the necessary resources are available, we&nbsp;have made arrangements
                to&nbsp;receive donations from those who wish to&nbsp;contribute to&nbsp;the effort, including
                in&nbsp;cryptocurrency and fiat currency.
              </InfoText>
              <DonateLink to={menu.donate}>
                Donate
                <Icon name='arrow' rotate='-90deg' />
              </DonateLink>
            </InfoWrap>

            <Composition>
              <ImgWrap>
                <StyledImage fluid={data.alex.childImageSharp.fluid} alt='AlexPertsev image' />
              </ImgWrap>
              <ImgWrap isShow={show}>
                <StyledImage fluid={data.roman.childImageSharp.fluid} alt='RomanStorm image' />
              </ImgWrap>
            </Composition>
          </Info>
        </Container>
      </Wrap>
    </Section>
  )
}

export { Whois }
