import React, { useState } from 'react'
import { numbers } from 'constant'

import { TweetLogoName, TweetAvatarImg } from './Style'

type Props = {
  name: string
  src: string
}

function TweetAvatar({ name, src }: Props) {
  const [error, setError] = useState(false)

  const alt = name + ' avatar'

  const imageOnErrorHandler = () => {
    setError(true)
  }

  return (
    <>
      {error ? (
        <TweetLogoName>{name.charAt(numbers.ZERO)}</TweetLogoName>
      ) : (
        <TweetAvatarImg src={src} alt={alt} onError={imageOnErrorHandler} />
      )}
    </>
  )
}

export { TweetAvatar }
