import styled from 'styled-components/macro'
import { Link } from 'gatsby'

export const StyledLink = styled(Link)`
  padding: 0;
  display: inline-flex;
  align-items: center;
  z-index: ${(props) => props.theme.zIndex.minimal};
`

export const Logo = styled.span`
  display: inline-flex;
  align-items: center;
  width: 12rem;
  flex: 0 0 auto;
  ${(props) => props.theme.breakpoints.greaterThan('lg')`
    width: 14rem;
  `};
`

export const LogoText = styled.span`
  font-family: ${(props) => props.theme.font.family.primary};
  font-weight: ${(props) => props.theme.font.weight.bold};
  font-size: 2rem;
  line-height: 1;
  display: none;
  ${(props) => props.theme.breakpoints.greaterThan('xsm')`
    display: block;
  `};
  ${(props) => props.theme.breakpoints.greaterThan('sm')`
    font-size: 2.4rem;
  `};
  ${(props) => props.theme.breakpoints.greaterThan('md')`
    font-size: 2rem;
  `};
  ${(props) => props.theme.breakpoints.greaterThan('lg')`
    font-size: 3.2rem;
  `};
`

export const Brake = styled.span`
  display: none;
  ${(props) => props.theme.breakpoints.greaterThan('md')`
    display: inline-block;
  `};
  ${(props) => props.theme.breakpoints.greaterThan('lg')`
    display: none;
  `};
`

export const LogoName = styled.span`
  font-weight: ${(props) => props.theme.font.weight.medium};
  font-size: 1.4rem;
  line-height: 1.43;
`

export const LogoSpan = styled.span`
  display: none;
`

export const IconWrapper = styled.span`
  display: flex;
  height: 4.2rem;
  cursor: pointer;
  ${(props) => props.theme.breakpoints.greaterThan('xsm')`
    width: 12rem;
  `};
  ${(props) => props.theme.breakpoints.greaterThan('lg')`
    width: 14rem;
  `};
`

export const IconDesktop = styled.span`
  width: 100%;
  display: none;
  flex: 0 0 auto;
  ${(props) => props.theme.breakpoints.greaterThan('xsm')`
    display: flex;
  `};
`

export const IconMobile = styled.span`
  width: 100%;
  display: flex;
  flex: 0 0 auto;
  ${(props) => props.theme.breakpoints.greaterThan('xsm')`
    display: none;
  `};
`
