import React, { useState } from 'react'

import { menu } from 'constant'

import { Icon, LogoLink, ScrollTo } from 'components/ui'

import {
  Nav,
  List,
  Wrap,
  Burger,
  Overlay,
  ListItem,
  Container,
  DonateLink,
  Connection,
  CustomLink,
  ListItemButton,
  ConnectionMobile,
} from './Style'

const Header = () => {
  const [isOpenNav, setOpenNav] = useState(false)

  const handleClick = () => {
    setOpenNav(!isOpenNav)
    document.body.style.overflowY = isOpenNav ? 'unset' : 'hidden'
    document.body.style.overflowX = 'hidden'
  }

  const handleClickMenu = () => {
    setOpenNav(!isOpenNav)
    document.body.style.overflowY = 'unset'
  }

  return (
    <Wrap id='appHeader'>
      <Container>
        <LogoLink />

        <Nav role='navigation' aria-label='Navigation' isOpen={isOpenNav}>
          <List>
            <ListItem>
              <ScrollTo to={menu.trial} href='/#trial'>
                <ListItemButton onClick={handleClickMenu}>
                  <CustomLink>Roman Storm’s Trial</CustomLink>
                </ListItemButton>
              </ScrollTo>
            </ListItem>
            <ListItem>
              <ScrollTo to={menu.whoIs} href='/#whoIs'>
                <ListItemButton onClick={handleClickMenu}>
                  <CustomLink>Who we are</CustomLink>
                </ListItemButton>
              </ScrollTo>
            </ListItem>
            <ListItem>
              <ScrollTo to={menu.important} href='/#important'>
                <ListItemButton onClick={handleClickMenu}>
                  <CustomLink>Why is it important?</CustomLink>
                </ListItemButton>
              </ScrollTo>
            </ListItem>
            <ListItem>
              <ScrollTo to={menu.help} href='/#help'>
                <ListItemButton onClick={handleClickMenu}>
                  <CustomLink>How can I help?</CustomLink>
                </ListItemButton>
              </ScrollTo>
            </ListItem>
            <ListItem>
              <ScrollTo to={menu.community} href='/#community'>
                <ListItemButton onClick={handleClickMenu}>
                  <CustomLink>Community feedback</CustomLink>
                </ListItemButton>
              </ScrollTo>
            </ListItem>
            <ListItem>
              <ScrollTo to={menu.faq} href='/#faq'>
                <ListItemButton onClick={handleClickMenu}>
                  <CustomLink>FAQ</CustomLink>
                </ListItemButton>
              </ScrollTo>
            </ListItem>
          </List>

          <Connection>
            <DonateLink to={menu.donate}>
              Donate
              <Icon name='arrow' rotate='-90deg' />
            </DonateLink>
          </Connection>
        </Nav>

        <ConnectionMobile>
          <DonateLink to={menu.donate}>
            Donate
            <Icon name='arrow' rotate='-90deg' />
          </DonateLink>
        </ConnectionMobile>

        <Burger isOpen={isOpenNav} onClick={handleClick} />
        <Overlay isOpen={isOpenNav} onClick={handleClickMenu} role='presentation' />
      </Container>
    </Wrap>
  )
}

export { Header }
